.card {
  &__format {
    display: flex;
    width: 48px;
    height: 48px;
    background: #1777ff;
    border-radius: 4px;
    align-items: center;
    justify-content: center;

    @include mobile {
      width: 34px;
      height: 34px;
    }

    & svg {
      background-size: cover;
    }
  }

  &__for-docs {
    svg {
      width: 32px;
      height: 33px;

      @include mobile {
        width: 23px;
        height: 24px;
      }
    }
  }

  &__for-pptx {
    svg {
      width: 26px;
      height: 32px;

      @include mobile {
        width: 20px;
        height: 24px;
      }
    }
  }

  &__for-live {
    svg {
      width: 32px;
      height: 32px;

      @include mobile {
        width: 23px;
        height: 23px;
      }
    }
  }

  &__for-table {
    svg {
      width: 32px;
      height: 32px;

      @include mobile {
        width: 24px;
        height: 24px;
      }
    }
  }

  &_time {
    display: flex;
    width: 112px;
    height: 112px;
    margin: 0 auto 32px;
    background: #fff;
    box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.02);
    border-radius: 12px;
    align-items: center;
    justify-content: center;

    &__small {
      width: 23px;
      height: 65px;
    }

    &__medium {
      width: 39px;
      height: 64px;
    }

    &__large {
      width: 64px;
      height: 65px;
    }

    @include mobile {
      width: 64px;
      height: 64px;
      margin-bottom: 24px;

      &__small {
        width: 40px;
        height: 40px;
      }

      &__medium {
        width: 34px;
        height: 34px;
      }

      &__large {
        width: 32px;
        height: 32px;
      }
    }
  }

  &_features {
    display: flex;
    width: 64px;
    height: 64px;
    background: #fff;
    box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.02);
    border-radius: 8px;
    align-items: center;
    justify-content: center;

    &__svg {
      width: 34px;
      height: 34px;
    }
  }
}
