.text {
  margin: 0;
  padding: 0;

  // font-family
  &_font-inter-regular {
    font-family: "InterRegular";
  }

  &_font-inter-medium {
    font-family: "InterMedium";
  }

  &_font-roboto-regular {
    font-family: "RobotoRegular";
  }

  &_font-roboto-medium {
    font-family: "RobotoMedium";
  }

  &_font-roboto-bold {
    font-family: "RobotoBold";
  }

  // font-size
  &_xs {
    font-size: 16px;

    @include mobile {
      font-size: 14px;
    }
  }

  &_sm {
    font-size: 18px;

    @include mobile {
      font-size: 16px;
    }
  }

  &_base {
    font-size: 20px;

    @include mobile {
      font-size: 14px;
    }
  }

  &_lg {
    font-size: 24px;

    @include mobile {
      font-size: 16px;
    }
  }

  &_xl {
    font-size: 30px;

    @include mobile {
      font-size: 24px;
    }
  }

  &_2xl {
    font-size: 34px;

    @include mobile {
      font-size: 24px;
    }
  }

  &_3xl {
    font-size: 36px;
  }

  &_4xl {
    font-size: 44px;

    @include mobile {
      font-size: 36px;
    }
  }

  &_5xl {
    font-size: 64px;

    @include mobile {
      font-size: 48px;
    }
  }

  // line-height
  &_line-100 {
    line-height: 100%;
  }

  &_line-130 {
    line-height: 130%;
  }

  &_line-120 {
    line-height: 120%;
  }

  &_line-155 {
    line-height: 155%;
  }

  &_line-160 {
    line-height: 160%;
  }

  &_line-180 {
    line-height: 180%;
  }

  &_uppercase {
    text-transform: uppercase;
  }

  &_center {
    text-align: center;
  }

  //color
  &_blue {
    color: #1777ff;
  }

  &_grey {
    color: #656565;
  }

  &_space-sm {
    letter-spacing: -0.02em;
  }
}
