.header {
  background-color: #fff;
  box-shadow: 0px 8px 48px rgba(0, 0, 0, 0.05);

  &__container {
    display: flex;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 1296px;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;

    @include big-desktop {
      width: 960px;
    }

    @include tablet {
      width: 100%;
      padding: 14px;
    }
  }

  &__logo {
    display: block;
  }

  &__info {
    font-family: InterRegular;
    font-size: 18px;
    color: #656565;

    @include tablet {
      display: none;
    }
  }

  @include mobile {

    &__logo {
      width: 93px;
      height: 20px;
    }
  }
}
