.list {
  margin: 0;
  padding: 0;
  list-style: none;

  .list__item {
    position: relative;
    padding-left: 16px;

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 5px;
      width: 8px;
      height: 8px;
      background: #ff4230;
      border-radius: 50%;
    }
  }

  &_numb {
    position: relative;
    list-style-type: none;
    counter-reset: num;
    font-size: 24px;

    &:after {
      content: "";
      position: absolute;
      left: 22px;
      top: 10px;
      bottom: 10px;
      border-left: 5px solid #c6ddff;

      @include mobile {
        left: 18px;
      }
    }

    & li {
      position: relative;
      padding-left: 72px;
      margin-bottom: 48px;
      font-size: 24px;
      color: #111;

      @include mobile {
        padding-left: 52px;
        font-size: 20px;
      }

      &:after {
        content: counter(num);
        counter-increment: num;
        display: inline-block;
        position: absolute;
        z-index: 1;
        left: 0;
        top: 50%;
        width: 48px;
        height: 48px;
        line-height: 48px;
        margin-top: -24px;
        font-family: "RobotoBold";
        font-size: 32px;
        text-align: center;
        color: #fff;
        background-color: #1777ff;
        border-radius: 50%;

        @include mobile {
          width: 40px;
          height: 40px;
          line-height: 40px;
          font-size: 24px;
        }
      }

      &:first-child {
        &:before {
          content: "";
          display: inline-block;
          position: absolute;
          z-index: 1;
          left: 0;
          top: 0;
          width: 44px;
          height: 50%;
          background-color: #fafafa;
        }
      }

      &:last-child {
        margin-bottom: 0;

        &:before {
          content: "";
          display: inline-block;
          position: absolute;
          z-index: 1;
          left: 0;
          top: 50%;
          bottom: 0;
          width: 44px;
          height: 100%;
          background-color: #fafafa;
        }
      }
    }
  }
}
