.button {
  font-family: InterRegular;
  font-size: 18px;
  border-radius: 6px;
  border: 1px solid #5c5c5c;
  background: none;
  cursor: pointer;

  &_grey {
    position: relative;
    padding: 0 48px;
    height: 48px;
    line-height: 48px;
    border-color: #5c5c5c;
    color: #5c5c5c;
    overflow: hidden;
    transition: all 850ms ease-in-out;

    &:after {
      content: "";
      position: absolute;
      top: -50px;
      left: -75px;
      width: 50px;
      height: 155px;
      background: #d6d3d3;
      opacity: 0.1;
      transform: rotate(35deg);
      transition: all 850ms cubic-bezier(0.19, 1, 0.22, 1);
    }

    @include hover {
      &:hover {
        border-color: rgba($color: #000000, $alpha: 0.3);

        &:after {
          left: 120%;
        }
      }

      &:active {
        border-color: #5c5c5c;
      }
    }

    @include mobile {
      height: 32px;
      line-height: 32px;
      padding: 0 20px;
      font-size: 12px;
    }
  }

  &_blue {
    height: 56px;
    padding: 0 60px;
    border: none;
    color: #fff;
    background: radial-gradient(42.87% 100% at 50.16% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #1777FF;
    box-shadow: 0px 12px 24px 0px rgba(23, 119, 255, 0.2);
    border-radius: 6px;
    transition: all 850ms ease-in-out;

    @include hover {
      &:hover {
        background-color: #0d1e38;
      }

      &:active {
        box-shadow: 0px 12px 24px 0px rgba(23, 119, 255, 0);
      }
    }

    @include mobile {
    }
  }
}
