html {
  box-sizing: border-box;
  background-color: #f5f5f5;
  color: #0d1e38;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  display: flex;
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  flex-direction: column;
  justify-content: space-between;
  overflow-x: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.image{
  &-fit {
    max-width: fit-content;

    @include tablet {
      width: 100%;
    }
  }
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.icon {
  &_login {
    display: inline-block;
    padding-right: 21px;
    background: url("data:image/svg+xml;base64,") no-repeat right center;
    background-size: 14px 15px;

    @include mobile {
      font-size: 12px;
      background-size: 11px 12px;
    }
  }
}

.main {
  padding-top: 84px;
  padding-bottom: 94px;

  @include big-desktop {
    padding-top: 96px;
    padding-left: 12px;
    padding-right: 12px;
  }

  @include mobile {
    padding-top: 54px;
    padding-bottom: 64px;
  }
}

.main-block {
  position: relative;
  margin: 0 auto;
  width: 1296px;
  min-height: 581px;

  &__info {
    width: 414px;
    padding-top: 96px;
  }

  &__picture {
    position: absolute;
    left: 440px;
    top: 0;
    filter: drop-shadow(-24px 0px 64px rgba(0, 0, 0, 0.03));
  }

  @include big-desktop {
    width: 960px;
  }

  @include tablet {
    width: 100%;
    min-height: auto;

    &__info {
      padding-top: 0;
      width: 100%;
      text-align: center;
      padding: 14px;
    }

    &__picture {
      display: block;
      margin-top: 13px;
      width: 100%;
      position: relative;
      left: 0;
    }

    &__list {
      display: inline-block;
    }

    &__button {
      display: block;
      margin: 0 auto;
    }
  }
}

.what-we-do {
  position: relative;
  padding-top: 107px;
  padding-bottom: 118px;
  justify-items: stretch;

  &__svg {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -1280px;
  }

  &__container {
    position: relative;
    display: grid;
    width: 1296px;
    min-height: 343px;
    padding: 64px 24px;
    margin: 0 auto;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 24px;
    background-color: #fafafa;
    border-radius: 12px;
    align-items: center
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 23px;
    column-gap: 24px;
  }

  &__item {
    display: flex;
    column-gap: 14px;
    padding: 24px 16px;
    background: #fff;
    box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.02);
    border-radius: 8px;
    align-items: center;
  }

  @include big-desktop {
    &__container {
      width: 960px;
      padding: 24px;
      grid-template-columns: repeat(1, 1fr);
      row-gap: 24px;
    }
  }

  @include tablet {
    &__container {
      width: 100%;
    }

    &__svg {
      display: none;
    }
  }

  @include mobile {
    padding-top: 64px;
    padding-bottom: 64px;

    &__container {
      padding: 24px 14px;
    }

    &__items {
      grid-template-columns: repeat(1, 1fr);
    }
    &__item {
      padding: 14px;
    }
  }
}

.turnaround-time {
  &__container {
    position: relative;
    width: 1296px;
    margin: 0 auto;
    text-align: center;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 24px;
  }

  &__item {
    padding: 36px 16px;
    background: #fafafa;
    text-align: center;
    border-radius: 12px;
  }

  @include big-desktop {
    &__container {
      width: 960px;
    }
  }

  @include tablet {
    &__container {
      width: 100%;
    }

    &__items {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 24px;
    }
  }

  @include mobile {
    &__item {
      padding: 14px;
    }
  }
}

.project {
  position: relative;
  padding-top: 124px;

  @include mobile {
    padding-top: 48px;
  }

  &__svg {
    position: absolute;
    top: 36px;
    left: 50%;
    margin-left: -1180px;
  }

  &__steps {
    position: relative;
    display: flex;
    width: 1296px;
    padding: 43px 48px 43px 24px;
    margin: 0 auto;
    column-gap: 74px;
    background: #fafafa;
    border-radius: 12px;

    &__info {
      width: 100%;
    }

    @include mobile {
      padding-left: 14px;
      padding-right: 14px;
    }
  }

  &__performance {
    position: relative;
    width: 855px;
    margin: 124px auto 0;
    padding: 14px 24px;
    background: #fafafa;
    border-radius: 12px;

    @include mobile {
      margin-top: 64px;
    }
  }

  &__items {
    display: grid;
    padding-left: 28px;
    grid-template-columns: 240px 1fr 1fr 1fr;

    &:first-child {
      padding-bottom: 18px;
    }

    &:nth-child(n+2) {
      border-top: 4px solid #f5f5f5;
      padding-top: 10px;
    }
  }

  &__item {
    padding: 8px 0;

    &:first-child {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    &:nth-child(n+3) {
      border-left: 4px solid #f5f5f5;
    }
  }

  @include big-desktop {
    &__steps {
      width: 960px;
    }

    &__picture {
      width: 90%;
      overflow: hidden;
    }
  }

  @include tablet {
    &__steps {
      width: 100%;
    }

    &__picture {
      display: none;
    }

    &__svg {
      display: none;
    }

    &__performance {
      width: 100%;
      padding: 36px 12px;
    }

    &__items {
      padding-left: 0;
      grid-template-columns: repeat(3, 1fr);

      &:first-child {
        padding-bottom: 25px;
      }

      &:nth-child(n+2) {
        padding-top: 44px;
      }
    }

    &__item {
      padding-top: 0;
      padding-bottom: 0;

      &:first-child {
        margin-bottom: 30px;
        grid-column: 3 span;
        justify-content: center;
      }
    }
  }
}

.features {
  position: relative;
  padding-top: 124px;

  &__svg {
    position: absolute;
    top: -15px;
    left: 50%;
    margin-left: -1120px;
  }

  &__container {
    position: relative;
    width: 1297px;
    margin: 0 auto;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 24px;
    row-gap: 24px;
  }

  &__item {
    display: grid;
    grid-template-columns: repeat(2, auto);
    padding: 25px 40px 25px 24px;
    column-gap: 24px;
    background: #fafafa;
    border-radius: 6px;
    align-items: center;
    justify-content: start;
  }

  &__button {
    margin-top: 24px;
    grid-column: 2;
    text-transform: uppercase;
  }

  @include big-desktop {
    &__container {
      width: 960px;
    }

    &__items {
      grid-template-columns: repeat(4, 1fr);
    }

    &__item {
      grid-column: 2 span;
    }

    &__button {
      grid-column: 2/4;
    }
  }

  @include tablet {
    &__container {
      width: 100%;
    }

    &__items {
      grid-template-columns: repeat(1, 1fr);
    }

    &__button {
      grid-column: 2 span;
    }

    &__svg {
      display: none;
    }
  }

  @include mobile {
    padding-top: 64px;

    &__items {
      row-gap: 14px;
    }
  }
}
