.footer {
  background-color: #0d1e38;
  padding-top: 40px;
  padding-bottom: 34px;
  font-family: InterRegular;
  font-size: 16px;
  color: #fff;

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  & a {
    color: #fff;
    border-bottom: 1px solid #fff;
    transition: border .15s ease-in-out;

    &:hover {
      border-bottom: 1px solid (rgba($color: #fff, $alpha: 0));
    }
  }

  &__container {
    display: flex;
    height: 76px;
    width: 1296px;
    margin: 0 auto;
    height: auto;
    gap: 20px;
    align-items: center;
    justify-content: space-between;

    & p {
      margin: 0;
    }

    @include big-desktop {
      width: 960px;
    }

    @include tablet {
      width: 100%;
      padding: 14px;
      align-items: flex-start;
      flex-direction: column;
    }

    @include mobile {
      padding-top: 0;
      padding-bottom: 0;
      gap: 48px;
    }
  }

  &__logo {
    display: block;
    margin-bottom: 30px;
  }

  &__list {
    display: flex;
    padding: 0;
    margin: 0;
    gap: 14px;
    list-style: none;
    flex-direction: column
  }
}
